
<template>
    <v-form ref="form" @submit.prevent>
        <v-card elevation="50">
            <v-card-title
                v-text="this.user.username"
                class="font-weight-black justify-center"
                style="font-size:2em"
            ></v-card-title>
            <v-row justify="center" align="center">
                <v-col  md="4">
                    <v-card-subtitle
                        v-text="'工號: '+this.user.employee"
                        class="text-center mt-0"
                        style="font-size:1em"
                    ></v-card-subtitle>
                </v-col>
                <v-col md="4">
                    <v-card-subtitle
                        v-text="'到職日: '+this.user.board"
                        class="text-center mt-0"
                        style="font-size:1em"
                    ></v-card-subtitle>
                </v-col>
            </v-row>
            <v-row justify="center" align="center">
                <v-col  md="4">
                    <v-card-subtitle
                        v-text="'部門: '+this.user.department"
                        class="text-center mt-0"
                        style="font-size:1em"
                    ></v-card-subtitle>
                </v-col>
                <v-col  md="4">
                    <v-card-subtitle
                        v-text="'職位: '+this.user.job"
                        class="text-center mt-0"
                        style="font-size:1em"
                    ></v-card-subtitle>
                </v-col>
            </v-row>
        </v-card>       
    </v-form>
</template>


<script>

export default {
    name: "Account",  
    props: ['accountId'], 
    methods: {
        GetData() {          
            const api = `${process.env.VUE_APP_APIPATH}/users/accountdata/${this.user.accountId}`;
            this.$http({
            method: "GET",
            url: api,
            })
            .then((response) => {
                if (response.data.success) {
                    this.user.username=response.data.username;
                    this.user.board=response.data.board;
                    this.user.employee=response.data.employee;
                    this.user.department=response.data.department;
                    this.user.job=response.data.job;                    
                } else {
                    this.errorMessages=response.data.message;
                }
            })
            .catch((error) => {
                this.errorMessages=error;
            });                           
        },
    },
    watch:{
        accountId: {
            handler(newValue, oldValue) {
                if (newValue) {
                    this.user.accountId=newValue;
                    this.GetData();
                } else {
                    
                }
            },
            immediate: true,
        },
    },
    data(){
        return{
            errorMessages:"",
            user: {
                username: "", 
                accountId: "", 
                authority: "",
                board: "",
                employee: "",
                department: "",
                job:""
            },
        };
    },          
};
</script>