<template>
    <v-form ref="form" @submit.prevent>              
        <v-card elevation="50">
            <v-card-title
                v-text="'ATM清單'"
                class="justify-center"
                style="font-size:2em"
            ></v-card-title>   
             
            <v-card-text>
                <template v-for="(item, index) in atms">   
                    <v-expansion-panels focusable flat :key="`account_${item.Name}`">            
                    <v-expansion-panel  >
                        <v-expansion-panel-header class="pt-0 pb-0 pl-0">
                            <v-list-item>
                                <v-list-item-avatar>
                                    <v-icon
                                        class="grey lighten-1 white--text"
                                        v-text="`mdi-atm`"
                                    ></v-icon>
                                </v-list-item-avatar>

                                <v-list-item-content>
                                    <v-list-item-title
                                        class="font-weight-black"
                                        v-text="`${item.Name}`"
                                    ></v-list-item-title>

                                    <v-list-item-subtitle
                                        v-text="`${item.State1}`"
                                    ></v-list-item-subtitle>

                                    <v-list-item-subtitle
                                        v-text="`${item.State2}`"
                                    ></v-list-item-subtitle>
                                </v-list-item-content> 
                            </v-list-item>
                        </v-expansion-panel-header>

                        <v-expansion-panel-content>
                            <div class="mt-4 text-center">
                            <v-btn
                                color="primary"
                                v-text="`開`"
                                @click="SendCommand(item.Name, 'on1')"
                            ></v-btn>
                            <v-btn
                                text
                                outlined
                                class="ml-4"
                                v-text="`關`"
                                @click="SendCommand(item.Name, 'off1')"
                            ></v-btn>
                            </div>
                        </v-expansion-panel-content>
                    </v-expansion-panel>
                    </v-expansion-panels>               
                    <v-divider :key="`d${index}`"></v-divider>
                </template>
            </v-card-text>      
        </v-card>                          
    </v-form>
</template>

<script>
    export default {
        name: "AtmList", 
        methods:{
            GetList() {          
                const api = `${process.env.VUE_APP_APIPATH}/users/atmlist`;
                this.$http({
                method: "GET",
                url: api,
                })
                .then((response) => {
                    if (response.data.success) {
                        this.atms=response.data.message ;              
                    } else {
                        this.errorMessages=response.data.message;
                    }
                })
                .catch((error) => {
                    this.errorMessages=error;
                });                           
            },
            SendCommand(name,cmd) {          
                const api = `${process.env.VUE_APP_APIPATH}/users/atmcommand/${name}/${cmd}`;
                this.$http({
                method: "GET",
                url: api,
                })
                .then((response) => {
                    if (response.data.success) {
                                        
                    } else {
                        this.errorMessages=response.data.message;
                    }
                })
                .catch((error) => {
                    this.errorMessages=error;
                });                           
            },
            GetState() {          
                const api = `${process.env.VUE_APP_APIPATH}/users/atmstate/${this.atm.atmname}`;
                this.$http({
                method: "GET",
                url: api,
                })
                .then((response) => {
                    if (response.data.success) {
                        this.atm.atmname=response.data.atmname;
                        this.atm.state1=response.data.state1;
                        this.atm.state2=response.data.state2;                    
                    } else {
                        this.errorMessages=response.data.message;
                    }
                })
                .catch((error) => {
                    this.errorMessages=error;
                });                           
            },       
        }, 
        created() {
            this.GetList();
        },          
        data() {
            return {     
                atms: [] 
            };
        },  
        mounted() {
            this.timer = setInterval(this.GetList, 1000);  //函數不加()
        },
        beforeDestroy() {
            clearInterval(this.timer);
        } 
    };
</script>