<template>
    <v-toolbar
        src="../assets/BackgroundT.png"
        :height="`${225*($store.state.ScrH/1080)}px`"
   
    >
    <v-container>
        <v-row>          
            <v-col md="1"> 
                <v-list-item>                 
                <v-card-text                                                 
                ></v-card-text>  
                <v-menu          
                    offset-y
                    transition="scale-transition"
                    origin="left top"
                    nudge-top="-10"
                    :close-on-content-click="false"
                >
                    <template v-slot:activator="{ on }">                                                         
                        <v-icon color="white lighten-1" size="80" v-text="`mdi-apps`" v-on="on"></v-icon>             
                    </template>
                    
                    <v-card  color="black" elevation="0">  
                        <v-list-item>            
                            <v-card color="black" elevation="0">             
                                <v-card-title >
                                    <v-icon color="grey lighten-1" size="75" v-text="`mdi-numeric-10-circle`" @click="goToPage('CD001')"></v-icon>
                                </v-card-title>
                                <v-card-subtitle
                                    v-text="'硬幣入金機'"
                                    class="grey--text text-center"  
                                    style="font-size:1em"                
                                ></v-card-subtitle>
                            </v-card >      

                            <v-card color="black" elevation="0">             
                                <v-card-title >
                                    <v-icon color="grey lighten-1" size="75" v-text="`mdi-sync-circle`" @click="goToPage('CC001')"></v-icon>
                                </v-card-title>
                                <v-card-subtitle
                                    v-text="'硬幣循環機'"
                                    class="grey--text text-center" 
                                    style="font-size:1em"                  
                                ></v-card-subtitle>
                            </v-card >  

                            <v-card color="black" elevation="0">             
                                <v-card-title >
                                    <v-icon color="grey lighten-1" size="75" v-text="`mdi-cog-outline`" @click="goToPage('Setting')"></v-icon>
                                </v-card-title>
                                <v-card-subtitle
                                    v-text="'設定'"
                                    class="grey--text text-center" 
                                    style="font-size:1em"                  
                                ></v-card-subtitle>
                            </v-card >         
                        </v-list-item>

                    </v-card >                                                           
                </v-menu>
                </v-list-item> 
            </v-col>
            <v-col md="11">
                
            </v-col>
        </v-row>
    </v-container>
    </v-toolbar>
</template>

<script>
import {mapState,mapMutations} from "vuex";

export default {
    name: "TopLayout",  
    props: [],  
    methods: {
        goToPage(p) {
            this.$router.push({ name: p });
        },
        get(url,cb){
            var httpRequest = new XMLHttpRequest();
            httpRequest.open('GET', url, true);
            httpRequest.send();
            httpRequest.onreadystatechange = function () {
                if (httpRequest.readyState == 4 && httpRequest.status == 200) {
                    var json = httpRequest.responseText;
                    cb(json);
                }
            };	
        },       
        ...mapMutations([          
            "Com",
            "GetWH",
        ]),      
    },
    computed:{
        ...mapState([
          
        ]),
    },
    watch: {
        
    },
    created() {       
        this.$options.sockets.onmessage = (msg) => {
            //console.log(msg);
            if(msg.data=='who are you'){
                if(localStorage.getItem('myName')){
                    this.dvName=localStorage.getItem('myName');
                }else{
                    this.dvName='nobody'
                    localStorage.setItem('myName',this.dvName);
                }
            
                if(localStorage.getItem('myType')){
                    this.dvType=localStorage.getItem('myType');                        
                }else{
                    this.dvType='unknow'
                    localStorage.setItem('myType',this.dvType);
                }                   
                var obj = { User: this.dvName, Command: "join"};                 
                this.$socket.send(JSON.stringify(obj));
            }else{
                //console.log(JSON.parse(msg.data));
                this.Com(JSON.parse(msg.data));             
            }
        }

        this.get("https://api.ipify.org/?format=json",function(data){
            data=JSON.parse(data);
            console.log(data.ip);
        });    
    },
    data() {
        return { 
            users: [],
        };
    }, 
    mounted() {
        this.timer1 = setInterval(this.GetWH, 500);  //函數不能加()
    },
    beforeDestroy() {
        clearInterval(this.timer1);
    },  
};
</script>

