import Vue from 'vue';
import Vuex from 'vuex';

Vue.use(Vuex);


// 定義一個新的 Vue Store
const store = new Vuex.Store({
    state: {
      WebW:"",
      WebH:"",
      ScrH:"1080",

      sound:null,
      volume:0.5,  

      isLoading: false,
      clickedTimes:0,
      dvName:'1234',
      dvType:'',

      DOL1:new Array(10),
      DOL5:new Array(10),
      DOL10:new Array(10),
      DOL20:new Array(10),
      DOL50:new Array(10),
      DOLR:new Array(10),
      DOLF:new Array(10), 
      TOT:new Array(10), //C300錢幣數量	
	    CTST:new Array(10).fill(new Array(2)), //C300狀態

      CHED:new Array(10).fill(new Array(10)),  //Hopper END Sensor
      CHNE:new Array(10).fill(new Array(10)),  //Hopper Near END Sensor
      CHST:new Array(10).fill(new Array(10)),  //Hopper 狀態
      CHCO:new Array(10).fill(new Array(10)),  //Hopper 吐幣數
      CHRS:new Array(10).fill(new Array(10)),  //Hopper 執行結果


      CRST:new Array(10).fill(new Array(3)),   //Escrow 狀態
	    CRRS:[[]],     //Escrow 執行結果
	    CRLK:[[]],       //Escrow 鎖
	    CRSW:[[]],      //Escrow 開關狀態
	    CRPI:[[]],          //Escrow 紅外線狀態

      img:'',
    },
    mutations: {
      // 將state設定為參數
      Loaded(state) {
        // state的isLoading true/false 互轉
        state.isLoading = !state.isLoading
      },
      GetWH(state){
        state.WebW=document.body.clientWidth;
        state.WebH=document.body.clientHeight;
        state.ScrH=window.screen.height;
      },
      ChangeName(state,N){
        state.dvName=N;
      },
      ChangeType(state,T){
        state.dvType=T;
      }, 
      Com(state,rx){
        if(rx.Command=="image"){
          if(rx.Parameter!=null){
            state.img=rx.Parameter[0];
            //console.log(rx.Parameter[0]);
          }        
        }else{
          console.log(rx);
        }
      },   
    }
})
export default store;