<template>
    <div>
        <TopLayout 
            :username.sync="user.username"
            :accountId.sync="user.accountId"
            :authority.sync="user.authority"
            :left.sync="left"
        >
        </TopLayout>  
        
        <v-container class="grey lighten-5">
            <v-row no-gutters>
                <v-col md="2">
                    <LeftLayout
                        :author.sync="user.authority"
                        :left.sync="left"
                    >
                    </LeftLayout>
                </v-col>
                <v-col md="10">
                    <router-view 
                            :accountId.sync="user.accountId"                           
                    ></router-view>  
                </v-col>
            </v-row>
        </v-container>               
    </div>
</template>

<script>
import TopLayout from '../components/TopLayout.vue'
import LeftLayout from '../components/LeftLayout.vue'

export default {
    name: "Features",  
    components: {
      TopLayout,
      LeftLayout,
    },
    methods: {

    },
    watch:{
        $route: {
            handler: function(to, from) {               
                this.left=to.name.substring(0,1);                                                                         
            },
            immediate: true,
        },      
    },
    data(){
        return{
            errorMessages:"",
            user: {
                username: "123", 
                accountId: "", 
                authority: "",
                board: "",
                employee: "",
                department: "",
                job:""
            },
            left:"",
        };
    },          
};
</script>