<template>
    <div>
        <v-card elevation="0">
            <template v-for="(item) in butts">
                <v-list-item :key="`${item}`" v-if="chauthor>=parseInt(item.authority)" @click="button(`${item.link}`)">
                    <v-list-item-avatar>
                        <v-icon
                            color="grey darken-2"
                            v-text="`${item.icon}`"
                        ></v-icon>
                    </v-list-item-avatar>

                    <v-list-item-content>
                        <v-list-item-title v-text="`${item.title}`"></v-list-item-title>
                    </v-list-item-content>
                </v-list-item>
            </template>                             
        </v-card>
    </div>
</template>


<script>

export default {
    name: "LeftLayout",  
    props: ['left'],   
    methods: {
        button(L) {            
            this.$router.push({ name: L });          
        },
        GetButts(btnname) {          
            const api = `${process.env.VUE_APP_APIPATH}/users/btnlist/${btnname}`;
            this.$http({
            method: "GET",
            url: api,
            })
            .then((response) => {
                if (response.data.success) {
                    this.butts=response.data.message ;              
                } else {
                    this.errorMessages=response.data.message;
                }
            })
            .catch((error) => {
                this.errorMessages=error;
            });                           
        },       
    },
    computed: {
             
    }, 
    watch: {
        author: {
            handler(newValue, oldValue) {
                if (newValue) {
                    this.chauthor=newValue;
                }
            },           
            immediate: true,
        },
        left: {
            handler(newValue, oldValue) {
                if (newValue) {
                    //this.GetButts(newValue)
                }
            },           
            immediate: true,
        },
    },
    created() {
       
    },
    data() {
        return {     
            chauthor: "9",
            butts:[ {
                    "title": "",
                    "icon": "",
                    "link": "",
                    "No": "1",
                    "authority": "1"
                }, 
                {
                    "title": "",
                    "icon": "",
                    "link": "",
                    "No": "2",
                    "authority": "1"
                }],
        };
    },   
};
</script>