<template>
  <v-form ref="form" @submit.prevent>
    <v-card>
      <div class="flexbox">
        <div class="calculator">
          <v-card-text            
                class="display"         
                style="font-size:0.65em"
                v-text="current"
          ></v-card-text>      
          <div class="btn dark" @click="clear">C</div>
          <div class="btn dark" @click="sign">+/-</div>
          <div class="btn dark" @click="percent"></div>
          <div class="btn operator" @click="divide">/</div>
          <div class="btn" @click="append('7')">7</div>
          <div class="btn" @click="append('8')">8</div>
          <div class="btn" @click="append('9')">9</div>
          <div class="btn operator" @click="multiply">x</div>
          <div class="btn" @click="append('4')">4</div>
          <div class="btn" @click="append('5')">5</div>
          <div class="btn" @click="append('6')">6</div>
          <div class="btn operator" @click="minus">-</div>
          <div class="btn" @click="append('1')">1</div>
          <div class="btn" @click="append('2')">2</div>
          <div class="btn" @click="append('3')">3</div>
          <div class="btn operator" @click="plus">+</div>
          <div class="btn zero" @click="append('0')">0</div>
          <div class="btn" @click="dot">.</div>
          <div class="btn operator" @click="equal">=</div>
        </div>
      </div>
    </v-card>
  </v-form>
</template>

<script>
import TopLayout from '../components/TopLayout.vue'

export default {
  name: "Calculator",
  components: {
      TopLayout,
  },
  data() {
    return {
      current: "",
      previous: "",
      operator: null,
      operatorClicked: false,
      errorMessages:"",
      user: {
          username: "123", 
          accountId: "", 
          authority: "",
          board: "",
          employee: "",
          department: "",
          job:""
      },
    };
  },  
  watch: {
    current: {
      immediate: true,
      handler() {
        const display = document.querySelector(".display");
        if (this.current.length > 30) {
          this.current = this.current.slice(0, 30);
        }
      }
    }
  },
  methods: {
    sign() {
      this.current =
        this.current.charAt(0) === "-"
          ? this.current.slice(1)
          : `-${this.current}`;
    },
    clear() {
      this.current = "";
      this.operator = null;
      this.operatorClicked = false;
    },
    percent() {
      this.current = this.current.slice(0, this.current.length-1);
      if(this.current.length==0){
        this.current = "0";
      }
      this.operator = null;
      this.operatorClicked = false;

    },
    append(number) {
      if (this.operatorClicked) this.current = "";
      this.operatorClicked = false;
      this.current =
        this.current === "0" ? `${number}` : `${this.current}${number}`;
    },
    setPrevious() {
      this.previous = this.current;
      this.operatorClicked = true;
    },
    plus() {
      this.operator = (a, b) => a + b;
      this.setPrevious();
    },
    equal() {
      console.log(this.current, this.previous);
      this.current = `${this.operator(
        parseFloat(this.previous),
        parseFloat(this.current)
      )}`;
      this.operator = null;
      this.operatorClicked = false;
    },
    minus() {
      this.operator = (a, b) => a - b;
      this.setPrevious();
    },
    multiply() {
      this.operator = (a, b) => a * b;
      this.setPrevious();
    },
    divide() {
      this.operator = (a, b) => a / b;
      this.setPrevious();
    },
    dot() {
      if (this.current.indexOf(".") === -1) {
        this.append(".");
      }
    }
  }
};
</script>

<style>  
  
.flexbox {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 70vw;
  height: 90vh;
}
  
.calculator {
  display: grid;
  width: 600px;
  margin: 0 auto;
  font-size: 40px;
  grid-template-columns: repeat(4, 1fr);
  grid-auto-rows: minmax(50px, auto);
  border: .3px solid #868383;
  border-radius: 3rem;
  padding: .8em;
  background-color: #222;
}
  
.display {
  height:50px;
  grid-column: 1/5;
  background-color: lightblue;
  text-align: right;
  padding-right: 20px; 
  background-color: #D3D3D3;
  color: #333;
  margin-bottom: 10px;
  border-radius: 16px;
  outline: 0;
  font-size: 36px;
}
  
.btn {
  border: 1px solid black;
  background-color: #999;
  cursor: pointer;
  border-radius: 40px;
  margin: 10px;
  color: #222;
  font-weight: bold;
  text-align: center;
  transition: background-color 0.2s ease-in;
}
  
.btn:hover {
 background-color: #868383;   
}
.dark {
  background-color: #333;
  color: #ffffff;
}
.operator {
  background-color: #e08d1f;
}
.zero {
  grid-column: 1/3;
}
</style>