<template>
    <div>     
        <TopLayout 
           
            >
        </TopLayout> 
        <v-container class="back"  fill-weight fluid >    
            <v-row style="height: 70px">
                 
            </v-row>        
            <v-row style="height: 200px">
                <v-col  md="4" >
                   <v-card-subtitle
                        v-text="'編號:'"                 
                        class="text-right mt-4 white--text"
                        style="font-size:4em"
                    ></v-card-subtitle>
                </v-col>
                <v-col  md="8">
                    <v-btn 
                        color='grey darken-2'                                                
                        class="white--text"  
                        v-text="`${dvName}`"  
                        style="height: 100px;width:600px;font-size:4em" 
                        @click="dialogShow = true"
                    ></v-btn> 
                </v-col>       
            </v-row>
            <v-row style="height: 870px">
                <!-- <v-col  md="4">
                   <v-card-subtitle
                        v-text="'機種:'"
                        class="text-right mt-4 white--text"
                        style="font-size:4em"
                    ></v-card-subtitle>
                </v-col>
                <v-col  md="8">                       
                    <v-btn color="primary" v-text="`${this.dvType}`"  style="height: 100px;width:600px;font-size:4em" ></v-btn>
                </v-col > -->
            </v-row> 
        </v-container> 

        <v-dialog v-model="dialogShow" max-width="540">
            <v-form ref="form" @submit.prevent>
                <v-card color="grey darken-3">
                    <v-card-title v-text="`輸入新的編號`" class="white--text" ></v-card-title>
                    <v-card-text>
                        <v-text-field
                            rounded                      
                            solo                                                  
                            type="text"
                            class="font-weight-bold"
                            style="height: 50px;font-size:2em "                          
                            clearable 
                            v-model="myName"
                            :rules="usernameRules"
                            :counter="usernameLength"
                        ></v-text-field>
                    </v-card-text> 

                    <v-card-text>
                        <v-list-item>   
                        <v-btn             
                            rounded
                            color='grey darken-2'                       
                            class="mt-4 white--text"
                            v-text="7"
                            style="height: 50px;width:150px;font-size:2em"
                            @click="keyin('7')" 
                        >             
                        </v-btn>
                        <v-spacer></v-spacer>
                        <v-btn 
                            rounded
                            color='grey darken-2'                       
                            class="mt-4 white--text"
                            v-text="8"
                            style="height: 50px;width:150px;font-size:2em "
                            @click="keyin('8')" 
                        >
                        </v-btn>
                        <v-spacer></v-spacer>
                        <v-btn 
                            rounded
                            color='grey darken-2'                       
                            class="mt-4 white--text"                   
                            v-text="9"
                            style="height: 50px;width:150px;font-size:2em"
                            @click="keyin('9')" 
                        ></v-btn>
                        </v-list-item>  
                        <v-list-item>   
                        <v-btn 
                            rounded
                            color='grey darken-2'                       
                            class="mt-4 white--text" 
                            v-text="4"
                            style="height: 50px;width:150px;font-size:2em"
                            @click="keyin('4')" 
                        >             
                        </v-btn>
                        <v-spacer></v-spacer>
                        <v-btn 
                            rounded
                            color='grey darken-2'                       
                            class="mt-4 white--text"
                            v-text="5"
                            style="height: 50px;width:150px;font-size:2em"
                            @click="keyin('5')" 
                        >
                        </v-btn>
                        <v-spacer></v-spacer>
                        <v-btn 
                            rounded
                            color='grey darken-2'                       
                            class="mt-4 white--text"                     
                            v-text="6"
                            style="height: 50px;width:150px;font-size:2em"
                            @click="keyin('6')" 
                        ></v-btn>
                        </v-list-item> 
                        <v-list-item>   
                        <v-btn 
                            rounded
                            color='grey darken-2'                       
                            class="mt-4 white--text" 
                            v-text="1"
                            style="height: 50px;width:150px;font-size:2em"
                            @click="keyin('1')" 
                        >             
                        </v-btn>
                        <v-spacer></v-spacer>
                        <v-btn 
                            rounded
                            color='grey darken-2'                       
                            class="mt-4 white--text"
                            v-text="2"
                            style="height: 50px;width:150px;font-size:2em"
                            @click="keyin('2')" 
                        >
                        </v-btn>
                        <v-spacer></v-spacer>
                        <v-btn 
                            rounded                         
                            color='grey darken-2'                       
                            class="mt-4 white--text"                      
                            v-text="3"
                            style="height: 50px;width:150px;font-size:2em"
                            @click="keyin('3')" 
                        ></v-btn>
                        </v-list-item>                        
                        <v-list-item>   
                        <v-btn 
                            rounded
                            color='grey darken-2'                       
                            class="mt-4 white--text"  
                            v-text="0"
                            style="height: 50px;width:150px;font-size:2em"
                            @click="keyin('0')" 
                        >             
                        </v-btn>
                        <v-spacer></v-spacer>
                        <v-btn 
                            rounded
                            color='grey darken-2'                       
                            class="mt-4 white--text" 
                            v-text="'<--'"
                            style="height: 50px;width:315px;font-size:2em"
                            @click="keyin('<')" 
                        >
                        </v-btn>                  
                        </v-list-item>
                    </v-card-text> 
                    <v-card-actions>                    
                        <v-btn 
                            rounded
                            color='grey darken-2'                       
                            class="mt-4 white--text" 
                            @click="dialogShow = false" 
                            style="height: 50px;width:150px;font-size:2em"
                            v-text="`取消`">
                        </v-btn>
                        <v-spacer></v-spacer>
                        <v-btn 
                            rounded
                            color='grey darken-2'                       
                            class="mt-4 white--text" 
                            @click="verifyUser()"
                            style="height: 50px;width:150px;font-size:2em" 
                            v-text="`修改`"
                        ></v-btn>
                    </v-card-actions>
                </v-card>
            </v-form>
        </v-dialog>            
    </div>
</template>

<script>
import TopLayout from '../components/TopLayout.vue'
import {mapState} from "vuex";
import {mapMutations} from "vuex";


export default {
    name: "Setting",  
    components: {
      TopLayout,  
    },
    methods: {
        verifyUser() {
                if (this.$refs.form.validate()) {
                    this.ChangeType(localStorage.getItem('myType'));
                    this.ChangeName('pc'+this.myName);
                    localStorage.setItem('myName',this.dvName);                    
                    this.dialogShow = false;
                    this.myName = '';
                    //this.$socket.send(this.dvName+':setting:'+this.dvType);
                }               
            },       
        keyin(k){
            if(this.myName==null){
                this.myName='';
            }
            if(k=='<'){
                this.myName = this.myName.slice(0, this.myName.length-1);
                if(this.myName.length==0){
                    this.myName = '';
                }
            }else{
                if (this.myName.length < this.usernameLength) {
                    this.myName+=k;
                }
            }
        },
        ...mapMutations([
            'ChangeName',
            'ChangeType'
        ]),
    },   
    computed:{
        ...mapState([
            'dvName',
            'dvType'
        ]),
    },
    watch:{
        $route: {
            handler: function(to, from) {               
                console.log('in')
                if(localStorage.getItem('myName'))
                {
                    this.ChangeName(localStorage.getItem('myName'));
                }else{
                    this.ChangeName('nobody');
                }                
                console.log(this.dvName)                                                                            
            },
            immediate: true,
        },          
    },
    data(){
        return{
            dialogShow: false,
            myName:'',
            usernameLength: 4,
            usernameRules: [
                (v) => !!v || "請輸入編號",
                (v) => (v && v.length == this.usernameLength) || "剛好4碼",                   
            ], 
        };
    },          
};
</script>


<style>
    .back{
        background-image: url('../assets/BackgroundM.png');
        background-size: cover;
        background-repeat: no-repeat;
    }

    .text-white input {
        color: black !important;  
    }
  

    
</style>