<template>
   <v-container >
        <v-row  :style="`height:${100*($store.state.ScrH/1080)}px`">
            <v-col align="left" justify="center" md="2" >
                <v-btn color="primary" v-text="`狀態` "  :style="`height: ${100*($store.state.ScrH/1080)}px;width:${(295*($store.state.WebW/1920))}px;`" @click="deviceCmd('state')"></v-btn>          
            </v-col>
            <v-col  md="2">
                <v-btn color="primary" v-text="`重置` " :style="`height: ${100*($store.state.ScrH/1080)}px;width:${295*($store.state.WebW/1920)}px;`" @click="deviceCmd('reset')"></v-btn>
            </v-col>
            <v-col  md="2">
                <v-btn color="primary" v-text="`A前進` " :style="`height: ${100*($store.state.ScrH/1080)}px;width:${295*($store.state.WebW/1920)}px;`" @click="deviceCmd('move',[49,49])"></v-btn>
            </v-col>
            <v-col  md="2">
                <v-btn color="primary" v-text="`A後退` " :style="`height: ${100*($store.state.ScrH/1080)}px;width:${295*($store.state.WebW/1920)}px;`" @click="deviceCmd('move',[49,50])"></v-btn>
            </v-col>
            <v-col md="2">
                <v-btn color="primary" v-text="`圖片2` " :style="`height: ${100*($store.state.ScrH/1080)}px;width:${295*($store.state.WebW/1920)}px;`" @click="deviceCmd('image',[2])"></v-btn>
            </v-col>
            <v-col  md="2">
                <v-btn color="primary" v-text="`ee` " :style="`height: ${100*($store.state.ScrH/1080)}px;width:${295*($store.state.WebW/1920)}px;`"></v-btn>
            </v-col>           
        </v-row>

        <v-row  :style="`height:${100*($store.state.ScrH/1080)}px`">         
          <v-col cols="5">
            <v-btn color="primary" v-text="`${$store.state.WebW}`"  :style="`height: ${100*($store.state.ScrH/1080)}px;width:${295*($store.state.WebW/1920)}px;`" ></v-btn>
          </v-col>
          <v-col cols="7">
            <v-btn color="primary" v-text="`${$store.state.WebH}` "  :style="`height: ${100*($store.state.ScrH/1080)}px;width:${295*($store.state.WebW/1920)}px;`"></v-btn>
          </v-col>
        </v-row>

        <v-row  :style="`height:${500*($store.state.ScrH/1080)-19}px`"> 
          <v-col cols="6">
            <v-img 
              :max-height="`${500*($store.state.ScrH/1080)}`"
              :max-width="`${600*($store.state.WebW/1920)}`" 
              :src="`data:image/png;base64,${$store.state.img}`"
            ></v-img>
            <v-btn color="primary" v-text="`ON`"  @click="CamEn=1"></v-btn>
            <v-btn color="primary" v-text="`OFF`"  @click="CamEn=0"></v-btn>
          </v-col>
          <v-col cols="2">
            <p class="white--text">提示音:</p>
            <template v-for="(item) in options">
                <v-list-item :key="`${item}`"  @click="audioplay(item)">
                    <v-list-item-avatar>
                        <v-icon
                            color="grey lighten-1"
                            v-text="`${item.icon}`"
                        ></v-icon>
                    </v-list-item-avatar>

                    <v-list-item-content>
                        <v-list-item-title class="white--text" v-text="`${item.label}`"></v-list-item-title>
                    </v-list-item-content>
                </v-list-item>
            </template>          
          </v-col>
          <v-col cols="4">
          </v-col>
        </v-row>

        <v-row  :style="`height:${150*($store.state.ScrH/1080)}px`">         
            <v-img  
                :max-height="`${100*($store.state.ScrH/1080)}`"
                :max-width="`${300*($store.state.WebW/1920)}`"
                src="../assets/btnLogoff.png"
                @click="goToPage('CD001')" 
            >
              <span class="my-span" :style="`padding: ${25*($store.state.ScrH/1080)}px;font-size:${2*($store.state.ScrH/1080)}em;`">
                返回
              </span>
            </v-img> 

            <v-spacer></v-spacer>   

            <v-img  
                :max-height="`${100*($store.state.ScrH/1080)}`"
                :max-width="`${300*($store.state.WebW/1920)}`"
                src="../assets/btnNextPage.png"
                @click="goToPage('CD001')" 
            >
              <span class="my-span" :style="`padding: ${25*($store.state.ScrH/1080)}px;font-size:${2*($store.state.ScrH/1080)}em;`">
                下一步
              </span>
            </v-img>
         
        </v-row>       
      </v-container>
      
</template>



<script>
import {mapState,mapMutations} from "vuex";


  export default {
    name: 'CD002',

    watch:{
     
    },
    methods: {
        audioplay(source){ 
          if(source.howl!=null) {
            if(source.howl.playing()){
              source.howl.stop();
              source.howl.play();
            }else{
              source.howl.play();
            }          
          }else{
            var sound = new Howl({
              src: [source.src,],
              volume: 0.5,
            });
            source.howl=sound;
            source.howl.play();
          }                   
        },
        goToPage(p){            
            this.$router.push({ name: p });          
        },
        deviceCmd(ccc,ppp) {
            this.dvName=localStorage.getItem('myName'); 
            var obj = { User: this.dvName, Command: ccc, Parameter:ppp};                 
            this.$socket.send(JSON.stringify(obj));
        },      
        vvv() {
          if(this.CamEn==1){
            this.deviceCmd('image',[2]); 
          }        
        },
        ...mapMutations([          
        
       
        ]), 
    },
    computed:{
        ...mapState([
          
        ]),
    },
    data() {
        return { 
            CamEn:1,
            dvName:'',
            dvType:'GCI_Depositer',
            img:'',
            options: [
              {
                src: require('../audio/請投入紙鈔或硬幣.mp3'),
                label: '請投入紙鈔或硬幣',
                icon:'mdi-alert-decagram-outline',
                howl:null,
              },{
                src: require('../audio/歡迎使用.mp3'),
                label: '歡迎使用',
                icon:'mdi-alert-decagram-outline',
                howl:null,
              },{
                src: require('../audio/請輸入密碼.mp3'),
                label: '請輸入密碼',
                icon:'mdi-alert-decagram-outline',
                howl:null,
              },{
                src: require('../audio/請選擇迴路.mp3'),
                label: '請選擇迴路',
                icon:'mdi-alert-decagram-outline',
                howl:null,
              },
            ],                
        };
    },
    created(){
    
    },   
    mounted() {
        this.timer2 = setInterval(this.vvv, 60);  //函數不能加()
    },
    beforeDestroy() {
        clearInterval(this.timer2);
    },
  }
</script>

<style>
    .my-span {      
      color: black;
      font-weight: bold;
      display:flex; 
      justify-content: center;   
    }
</style>