import Vue from 'vue'
import VueRouter from 'vue-router'
import Index from '../views/Index.vue'
import Features from '../views/Features.vue'
import Account from '../components/Account.vue'
import AtmList from '../components/AtmList.vue'
import Money from '../components/Money.vue'
import Test from '../components/Test.vue'
import ChartBar from '../components/ChartBar.vue'
import CoinDepositer from '../views/CoinDepositer.vue'
import Setting from '../views/Setting.vue'
import CD001 from '../components/CD001.vue'
import CD002 from '../components/CD002.vue'
import CoinCycler from '../views/CoinCycler.vue'
import CC001 from '../components/CC001.vue'
import CC002 from '../components/CC002.vue'

Vue.use(VueRouter)

const mixProps = function () {
  return route => {
    return Object.assign({}, route.params, route.query)
  }
}

const routes = [
  {
    path: '/',
    name: 'Index',
    component: Index
  },
  {
    path: '/coindepositer',
    name: 'CoinDepositer',
    component: CoinDepositer,
    props: mixProps(),
    children: [
      {
        path: 'cd001',
        name: 'CD001',
        component: CD001,
      },
      {
        path: 'cd002',
        name: 'CD002',        
        component: CD002,
      },
    ]
  },
  {
    path: '/coincycler',
    name: 'CoinCycler',        
    component: CoinCycler,
    props: mixProps(),
    children: [
      {
        path: 'cc001',
        name: 'CC001',
        component: CC001,
      },
      {
        path: 'cc002',
        name: 'CC002',        
        component: CC002,
      },
    ]
  },
  {
    path: '/setting',
    name: 'Setting',        
    component: Setting,
    children: [
      
    ]
  },
]



const router = new VueRouter({
  mode: 'history',
  base: process.env.BASE_URL,
  routes
})

export default router
