<template>
    <div>
         
        <TopLayout 
           
                >
            </TopLayout> 
        <v-container class="back"  fill-weight fluid >            
            <v-row>
                              
                <v-col md="12">
                    <router-view 
                                                  
                    ></router-view>  
                </v-col>
            </v-row>
        </v-container>               
    </div>
</template>

<script>
import TopLayout from '../components/TopLayout.vue'
import LeftLayout from '../components/LeftLayout.vue'


export default {
    name: "CoinDepositer",  
    components: {
      TopLayout,
      LeftLayout,
    },
    methods: {

    },
    watch:{
        $route: {
            handler: function(to, from) {   
                if(to.name=='CD002'){           
                    //console.log('GCI_Depositer')                           
                    localStorage.setItem('myType','GCI_Depositer'); 
                    this.dvName=localStorage.getItem('myName');
                    var obj = { User: this.dvName, Command: "use"};                 
                    this.$socket.send(JSON.stringify(obj));
                    //this.$socket.send(this.dvName+':setting:GCI_Depositer');   
                }                                                                  
            },
            immediate: true,
        },      
    },
   
    data(){
        return{
            errorMessages:"",
            user: {
                username: "123", 
                accountId: "", 
                authority: "",
                board: "",
                employee: "",
                department: "",
                job:""
            },
            left:"",
        };
    },          
};
</script>


<style>
    .back{
        background-image: url('../assets/BackgroundM.png');
        background-size: cover;
        background-repeat: no-repeat;
    }
